import React, { useContext, useState } from 'react';
import { useLocation } from 'react-router-dom';
import { Popover, Select, Space } from 'antd';
import Context from '../../context/context';
import './filters.css';

import { PLATFORM_TYPES, ESTABLISHMENT_TYPES } from '../../constants';

const FiltersContent = () => {
  const {
    noPlatformFilteredReviews,
    selectedPlatform,
    updateSelectedPlatform,
    selectedBusinessType,
    updateSelectedBusinessType
  } = useContext(Context);

  const platformTypes = [
    {
      label: 'All',
      value: ''
    }, ...PLATFORM_TYPES.map(p => ({
      label: p,
      value: p,
    }))
  ];

  const establishmentTypes = [
    {
      label: 'All',
      value: ''
    }, ...ESTABLISHMENT_TYPES.map(p => ({
      label: p,
      value: p,
    }))
  ];

  return (
    <Space direction='vertical' size='middle' style={{ display: 'flex' }}>
      {selectedPlatform}
      <div>
        <h5 className='filters-select-label'>Plarform:</h5>
        <Select
          style={{width: '100%'}}
          variant='filled'
          value={selectedPlatform}
          onChange={updateSelectedPlatform}
          options={platformTypes}
        />
      </div>
      <div>
        <h5 className='filters-select-label'>Business type:</h5>
        <Select
          style={{width: '100%'}}
          variant='filled'
          value={selectedBusinessType}
          onChange={updateSelectedBusinessType}
          options={establishmentTypes}
        />
      </div>
    </Space>
  );
}


const Filters = (props) => {
  let location = useLocation();
  const [open, setOpen] = useState(true);

  const handleOpenChange = (newOpen) => {
    setOpen(newOpen);
  };

  return  location.pathname !== '/' ? (
    <Popover
      trigger='click'
      overlayClassName='filters-popover'
      content={<FiltersContent />}
      open={open}
      onOpenChange={handleOpenChange}
    >
      <button className='button-main filters_button'>
        <i className='filters_button_icon'>
          <img
            className='filters_button_icon-image'
            src={require('./../../assets/icons/filters.svg').default}
          />
        </i>
        Filters
      </button>
    </Popover>
  ) : ''
}

Filters.propTypes = {};

Filters.defaultProps = {};

export default Filters;
