import React, { useState, useContext, useEffect } from 'react';
import { Col, Row } from 'antd';
import Section from '../section/section.js';
import ChartCard from '../chart-card/chart-card';
import Context from '../../context/context';
import PieChart from '../pie-chart/pie-chart.js';
import { ESTABLISHMENT_TYPES } from '../../constants.js';

import './business-type-distribution.css';

const BusinessTypeDistribution = () => {

  let {
    filteredEstablishments
  } = useContext(Context);

  const [piesData, setPiesData] = useState(computePiesData());

  function computePiesData() {
    const emptyAcc = ESTABLISHMENT_TYPES.reduce((acc, type) => {
      acc[type] = {
        'Unknown': 0,
      };
      return acc;
    }, {});

    return filteredEstablishments.reduce((establishmentAcc, establishment)=>{
      if(!establishment.subcategory) {
        establishmentAcc[establishment.type]['Unknown'] +=1;
        return establishmentAcc;
      }
      establishment.subcategory.split(',').forEach(subcategory => {
        if(!establishmentAcc[establishment.type][subcategory]) {
          establishmentAcc[establishment.type][subcategory] = 1;
        } else {
          establishmentAcc[establishment.type][subcategory] += 1;
        }
      });
      return establishmentAcc;
    }, emptyAcc)
  }

  useEffect(() => {
    setPiesData(computePiesData());
  },[filteredEstablishments]);

  return (
    <Section title='Distribution of Types'>
      <Row style={{ display: 'flex', alignItems: 'stretch' }}>
        {
          ESTABLISHMENT_TYPES.map((type, index) => {
            return (
              <Col flex="auto" className='distribution-item-container' key={index} span={8}>
                <Section title={(<span><b>{type}</b> types</span>)}>
                  <ChartCard>
                    <PieChart
                      data={piesData[type]}
                      id={`pie-${type}`}
                      hasLegend
                    />
                  </ChartCard>
                </Section>
              </Col>
            )
          })
        }
      </Row>
    </Section>
  )
};

BusinessTypeDistribution.propTypes = {};

BusinessTypeDistribution.defaultProps = {};

export default BusinessTypeDistribution;
