import React, { useState, useContext, useEffect } from 'react';
import Section from '../section/section';
import BarChart from '../bar-chart/bar-chart';
import ChartCard from '../chart-card/chart-card';
import EstablishmentTypeToggle from '../establishment-type-toggle/establishment-type-toggle';
import Context from '../../context/context';

import './region-destribution.css';

const RegionDestribution = () => {

  const {
    filteredReviews
  } = useContext(Context);
  
  function computeBarsData() {
    const data = filteredReviews.reduce((reviewAcc, review)=>{
      if(review.district && reviewAcc[review.district]) {
        reviewAcc[review.district] +=1
      } else if (review.district) {
        reviewAcc[review.district] = 1
      } else {
        reviewAcc['Unknown'] +=1
      }
      return reviewAcc
    }, {Unknown: 0})
    if(data.Unknown === 0) {
      delete data.Unknown
    }
    
    return Object.keys(data).map(key => {
      return {
        key: key,
        value: data[key]
      }
    });
  }

  const [data, setData] = useState(computeBarsData());

  useEffect(() => {
    setData(computeBarsData());
  },[filteredReviews]);

  return (
    <Section title={(<span>Number of visits <b>over regions</b></span>)}>
      <EstablishmentTypeToggle />
      <ChartCard>
        <BarChart
          id='regions'
          hasLegend={false}
          data={data}
        />
      </ChartCard>
    </Section>
  )
};

RegionDestribution.propTypes = {};

RegionDestribution.defaultProps = {};

export default RegionDestribution;
