import React, { useContext, useState, useEffect } from 'react';
import { Col, Row } from 'antd';
import Section from '../section/section';
import ChartCard from '../chart-card/chart-card';
import BarChart from '../bar-chart/bar-chart';
import PieChart from '../pie-chart/pie-chart';
import Context from '../../context/context';

import './seasons-regions-gender-stats.css';

const seasonsCounter = (props) => {
  const { value, text } = props;
  return (
    <div className='seasons-counter'>
      <span className='seasons-counter-value'>{value}</span>
      <span className='seasons-counter-text'>{text}</span>
    </div>
  )
}

const SeasonsRegionsGenderStats = (props) => {
  const {
    filteredReviews
  } = useContext(Context);

  function computeSeasonVisits() {
    const seasonKeyList = [];
    const seasonVisitMap = {};

    const datelist = [...filteredReviews].sort((a,b) => {
      return b.date - a.date;
    })

    datelist.forEach((review) => {
      const date = new Date(review.date);
      const month = date.getMonth();
      let season;

      if (month < 2 || month === 11) {
        season = 'Winter';
      } else if (month < 5) {
        season = 'Spring';
      } else if (month < 8) {
        season = 'Summer';
      } else {
        season = 'Autumn';
      }
      let seasonKey = `${season} ${date.getFullYear()}`;
      if(!seasonKeyList.includes(seasonKey)) {
        seasonKeyList.push(seasonKey);
      }
      seasonVisitMap[seasonKey] ? seasonVisitMap[seasonKey] += 1 : seasonVisitMap[seasonKey] = 1;
    })
    return seasonKeyList.map(seasonKey => {
      return {
        key: seasonKey,
        value: seasonVisitMap[seasonKey]
      }
    })
  }

  function computeRegionVisits() {
    const regionMap = filteredReviews.reduce((reviewAcc, review)=>{
      if(reviewAcc[review.location]) {
        reviewAcc[review.location] +=1
      } else {
        reviewAcc[review.location] = 1
      }
      return reviewAcc
    }, {})
    return Object.keys(regionMap).map(regionKey => {
      return {
        key: regionKey,
        value: regionMap[regionKey]
      }
    }).sort((a,b) => {
      return b.value - a.value;
    })
  }

  function computeVisitorGenders() {
    return filteredReviews.reduce((reviewAcc, review)=>{
      if(reviewAcc[review.reviewer_gender_prediction]) {
        reviewAcc[review.reviewer_gender_prediction] +=1
      } else {
        reviewAcc[review.reviewer_gender_prediction] = 1
      }
      return reviewAcc
    }, {})
  }

  const [seazonVisits, seSeazonVisits] = useState(computeSeasonVisits());
  const [regionVisits, setRegionVisits] = useState(computeRegionVisits());
  const [visitorsGender, setVisitorsGender] = useState(computeVisitorGenders());

  useEffect(() => {
    setRegionVisits(computeRegionVisits());
    setVisitorsGender(computeVisitorGenders());
    seSeazonVisits(computeSeasonVisits());
  },[filteredReviews]);

  return (
    <>
      <Row gutter={[24, 24]}>
        <Col span={14}>
          <Section
            title={(<span>Number of visits based on <b>seasons</b></span>)}
          >
            <ChartCard>
              <BarChart
                id='seasons'
                data={seazonVisits}
                hasLegend={false}
                xTickTotated={true}
              />
            </ChartCard>
          </Section>
        </Col>
        <Col span={10}>
          <Section
            title={(<span>Visitors <b>gender</b></span>)}
          >
            <ChartCard>
              <PieChart
                id='gender'
                height={200}
                legendRight={true}
                data={visitorsGender}
                hasLegend={true}
              />
            </ChartCard>
          </Section>
        </Col>
      </Row>
      <Row>
        <Col span={24}>
          <Section
            title={(<span>Number of visits based on <b>origin of the visitor</b></span>)}
          >
            <ChartCard>
              <BarChart
                id='regions-visits'
                height={300}
                data={regionVisits}
                xTickTotated={true}
              />
            </ChartCard>
          </Section>
        </Col>
      </Row>
    </>
  );
}

SeasonsRegionsGenderStats.propTypes = {};

SeasonsRegionsGenderStats.defaultProps = {};

export default SeasonsRegionsGenderStats;
