import axios from 'axios';

function getReviews() {
  return axios.get(`/reviews.json`).then(response => response.data.map(o => ({
    ...o,
    date: new Date(o.travel_date)
  })))
}

function getEstablisments() {
  return axios.get(`/establisments.json`).then(response => response.data)
}

const service = {
  getReviews,
  getEstablisments,
}
export default service
