import { useEffect, useState, useReducer } from 'react';
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';
import './App.css';
import { Layout } from 'antd';
import Reducer from './context/reducer';
import Context from './context/context';
import Navigation from './components/navigation/navigation';
import Filters from './components/filters/filters';
import StatisticalInfo from './routes/statistical-info/statistical-info';
import Home from './routes/home/home';

import service from './service'

const { Header, Content, Footer, Sider } = Layout;

const siderStyle = {
  overflow: 'auto',
  height: '100vh',
  position: 'fixed',
  left: 0,
  top: 0,
  bottom: 0,
  width: 150,
  background: 'transparent'
};
const layoutStyle = {
  position: 'relative',
  zIndex: 1,
  background: 'transparent'
}
function App() {

  const initialState = {
    filteredReviews: [],
    filteredEstablishments: [],
    noPlatformFilteredReviews: [],
    selectedPlatform: '',
    selectedBusinessType: '',
  };
  
  const [state, dispatch] = useReducer(Reducer, initialState);

  const updateFilteredEstablishments = (filteredEstablishments) => {
    dispatch({
      type: 'UPDATE_FILTERED_ESTABLISHMENTS',
      payload: filteredEstablishments,
    });
  };

  const updateFilteredReviews = (filteredReviews) => {
    dispatch({
      type: 'UPDATE_FILTERED_REVIEWS',
      payload: filteredReviews,
    });
  };
  const updateSelectedBusinessType = (selectedBusinessType) => {
    dispatch({
      type: 'UPDATE_BUSINESS_TYPE',
      payload: selectedBusinessType,
    });
  };
  const updateNoPlatformFilteredReviews = (noPlatformFilteredReviews) => {
    dispatch({
      type: 'UPDATE_NO_PLATFORM_FILTERED_REVIEWS',
      payload: noPlatformFilteredReviews,
    });
  };
  const updateSelectedPlatform = (selectedPlatform) => {
    dispatch({
      type: 'UPDATE_SELECTED_PLATFORM',
      payload: selectedPlatform,
    });
  };

  const [reviews, setReviews] = useState([]);
  const [establishments, setEstablishments] = useState([]);

  function applyPlatformFilters(activeReviews) {
    return activeReviews.filter(r => {
      return state.selectedPlatform === '' || state.selectedPlatform === r.data_platform
    })
  }
  function applyNoPlatformFilters(activeReviews) {
    return activeReviews.filter(r => {
      return state.selectedBusinessType === '' || state.selectedBusinessType === r.establishment_type
    })
  }
  useEffect(() => {
    service.getReviews().then(setReviews)
    service.getEstablisments().then(setEstablishments)
  },[])

  useEffect(() => {
    const noPlatformFilters = applyNoPlatformFilters(reviews)
    updateFilteredEstablishments(establishments)
    updateNoPlatformFilteredReviews(noPlatformFilters)
    updateFilteredReviews(applyPlatformFilters(noPlatformFilters))
  },[reviews, establishments, state.selectedPlatform, state.selectedBusinessType])

  return (
    <div className='App'>
      <BrowserRouter>
        <Context.Provider
          value={{
            ...state,
            updateSelectedPlatform,
            updateSelectedBusinessType,
          }}
        >
          <Layout hasSider style={layoutStyle}>
            <Sider
              style={siderStyle}
            >
              <Navigation/>
            </Sider>
            <Layout
              style={{
                marginLeft: 150,
                marginRight: 50,
                background: 'transparent'
              }}
            >
              <Header
                className='root_header'
                style={{
                  padding: 0,
                  background: 'transparent'
                }}
              >
                <Filters />
              </Header>
                <Content>
                  <Routes>
                    <Route element={<StatisticalInfo/>} path='/intro' />
                    <Route element={<Home/>} path='/' />
                    {/* <Route index element={<Navigate to="/" replace />} /> */}
                  </Routes>
                </Content>
              <Footer
                style={{
                  background: 'transparent'
                }}
              >
              </Footer>
            </Layout>
          </Layout>
        </Context.Provider>
      </BrowserRouter>
    </div>
  );
}

export default App;
