import React from 'react';
import { Col, Row, Carousel } from 'antd';
import { NavLink } from 'react-router-dom';

import { links } from '../../constants';

import './home.css';

const slides = [
  {
    image: '/banner1.png',
    title: 'Nakupenda Beach Nature Reserve',
    text: 'Nakupenda means i love you in Swahili.its a beautiful sandbank beach with white clean sand ,beautiful clear water for swimming and snorkeling.'
  },
  {
    image: '/banner1.png',
    title: 'Nakupenda Beach Nature Reserve 2',
    text: 'Nakupenda means i love you in Swahili.its a beautiful sandbank beach with white clean sand ,beautiful clear water for swimming and snorkeling.'
  }
]

const Link = (props) => {
  const {text, link, icon} = props;

  return (
    <NavLink
      className={({ isActive, isPending }) =>
        `home-navigation-link${isActive ? " home-navigation-link-active" : ""}`
      }
      to={link}
    >
      <img
        className='home-navigation-link_icon'
        src={require(`./../../assets/icons/${icon}.svg`)}
        alt={text}
      />
      {text}
    </NavLink>
  )
}

const Slide = (props) => {
  const {text, title, image} = props;

  return (
    <div className='home-slide'>
      <div
        className='home-slide-image'
        style={{backgroundImage: `url(${image})`}}
      />
      <h2 className='home-slide-title'>{title}</h2>
      <p className='home-slide-text'>{text}</p>
    </div>
  )
}

const Home = () => {
  return (
    <>
      <Row>
        <Col span={12}>
          <h1 className='home-title'>
            Let's explore
            <div className='home-title-accent'>Zanzibar</div>
          </h1>
          <nav className='home-navigation'>
            {
              links.map((link) => {
                return (
                  <Link
                    key={link.text}
                    text={link.text}
                    link={link.link}
                    icon={link.icon}
                  />
                )
              })
            }
          </nav>
          <div className='home-sponsors'>
            <img
              className='home-sponsors-image'
              src={require('./../../assets/sponsor1.png')}
            />
            <img
              className='home-sponsors-image'
              src={require('./../../assets/sponsor2.png')}
            />
            <img
              className='home-sponsors-image'
              src={require('./../../assets/sponsor3.png')}
            />
          </div>
        </Col>
        <Col span={12}>
          <Carousel
            dots={false}
            arrows={true}
            autoplay
          >
            {
              slides.map((slide,i) => {
                return (
                  <Slide
                    key={`${i}-slide`}
                    text={slide.text}
                    title={slide.title}
                    image={slide.image}
                  />
                )
              })
            }
          </Carousel>
        </Col>
      </Row>
    </>
  );
}

Home.propTypes = {};

Home.defaultProps = {};

export default Home;
