import React, { useContext } from 'react';
import Context from '../../context/context';

import { ESTABLISHMENT_TYPES } from '../../constants.js'
import './establishment-type-toggle.css';

const EstablishmentTypeToggle = () => {
  const {
    selectedBusinessType,
    updateSelectedBusinessType
  } = useContext(Context);

  return (
    <div className="establishment-type-toggle">
      {
        ESTABLISHMENT_TYPES.map((type, index) => {
          return (
            <button
              key={index}
              onClick={() => {type === selectedBusinessType ? updateSelectedBusinessType('') : updateSelectedBusinessType(type)}}
              className={`establishment-type-toggle-button button-main ${selectedBusinessType !== type ? 'button-main-inactive' : ''}`}
            >
              {type}
            </button>
          )
        })
      }
    </div>
  )
}

EstablishmentTypeToggle.propTypes = {};

EstablishmentTypeToggle.defaultProps = {};

export default EstablishmentTypeToggle;
